<template>
  <div>
    <banner></banner>
    <contactos></contactos>
  </div>
</template>

<script>
/* eslint-disable */
import Banner from "./contactos/Banner";
import Contactos from "./contactos/Contactos";

export default {
  components: {
    banner: Banner,
    contactos: Contactos,
  },
  metaInfo() {
    return {
      title: "Contactos - Ana Carolina Pereira - Clínica de Psicologia",
      meta: [
        {
          name: "description",
          content:
            "Visite a nossa clínica ou entre em contacto connosco através do nosso número de telefone ou do nosso formulário online.",
        },
        { name: "robots", content: "index,follow" },
        { property: "og:site_name", content: "Ana Carolina Pereira" },
        { property: "og:type", content: "website" },
        {
          property: "og:title",
          content: "Contactos - Ana Carolina Pereira - Clínica de Psicologia",
        },
        {
          property: "og:description",
          content:
            "Visite a nossa clínica ou entre em contacto connosco através do nosso número de telefone ou do nosso formulário online.",
        },
      ],
    };
  },
};
</script>

<style lang="scss" scoped>
</style>



